
footer{
    display: flex;
    background-color: hsl(0, 0%, 0%);
    color: white;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

ul{
    grid-column: 50%;
}
li{
    list-style: none;
    grid-column: 50%;
}
.element1_img{
    display: block;
    overflow: hidden;
    width: 100%;
    max-width: 200px;
}
.f_elemen2{
    margin-right: 2rem;
}

.f_logo{
    border-radius: 50%;
    width: 30%;
    margin-left: 1rem;
}

@media only screen and (min-width: 600px) {
    .f_logo{
        width:10%;
        margin-left: 10rem;
    }
    .f_elemen2{
        margin-right: 10rem;
    }
}
.f_red_link{
    list-style: none;
    text-decoration: none;
    color: white;
}
.copy{
    color: white;
}